/**
 * Default login widget for desktop
 * @author Edward J Morales
 */

bizagi.login_multiple.widgets.login.extend("bizagi.login_multiple.widgets.login", {}, {
    init: function(loginFacade, dataService) {
        this._super(loginFacade, dataService);
    },
    postRender: function(params) {
        var self = this;
        var template = self.loginFacade.getTemplate("normal-login");
        var content = self.getContent();
        self.params = params || {};

        self.params.domains = Object.keys(self.params.multipleAuthenticator).map(function (domainKey) {
            return {
                value: domainKey,
                displayName: self.params.multipleAuthenticator[domainKey]["domainDisplayName"]
            }
        });

        self.params.noDomains = self.params.domains.length === 0;
        self.params.uniqueDomain = self.params.domains.length === 1;

        if (params.authenticationType == "BizAgi" || params.authenticationType == "Bizagi" || params.authenticationType == "Mixed" || params.authenticationType == "Windows") {
            var enableOptions = "true";
        }

        // Render Form
        var form = $.tmpl(template, {
            loginConfig: self.params,
            domains: self.params.domains,
            isBizagiAuthentication: enableOptions,
            isAdminLoginRequired: bizagi.util.parseBoolean(self.params.isAdminLoginRequired),
            authenticationType: self.params.authenticationType
        });

        $("#form-content", content).append(form);

        // Create object of message controller
        self.messageController = new bizagi.login_multiple.message(self.loginFacade, $("#login-message-wrapper", content));

        self.whenVisible(form, function () {
            self.showStep(1);

            // Set user value
            if(self.getParameterByName('b_username')) {
                var username = self.getParameterByName('b_username');
                var dataArray = username.split('\\');
                self.removeReadOnly($("#user", content), false);

                switch (dataArray.length) {
                    case 1:
                        $("#user", content).val(decodeURI(dataArray[0]));
                        break;
                    case 2:
                        var tempDomain = decodeURI(dataArray[0]);
                        if(self.isValidDomain(tempDomain)) {
                            $("#user", content).val(decodeURI(dataArray[1]));
                            self.selectDomain(self, tempDomain);
                        }
                        break;
                    default:
                        break;
                }
            } else {
                // Set user value
                if(params.user) {
                    $("#user").val(params.user);
                }
                // Set domain value
                if(params.domain && self.isValidDomain(params.domain)) {
                    self.selectDomain(self, params.domain);
                } else if (self.params.selectedDomain) {
                    self.selectDomain(self, self.params.selectedDomain);
                }
            }

            // Check Login Options
            if(self.getParameterByName('b_origin') && self.getParameterByName('b_origin') === 'native') {
                $(".bzg-login__options").detach();
            } else if(params.selectedCheck) {
                var loginOption = "";

                if(params.selectedCheck == "saveAccount") {
                    loginOption = "rbSaveAccount";
                    $("#" + loginOption, ".bzg-login__options").attr("checked", "checked");
                } else {
                    loginOption = "rbAskAlways";
                }
            }

            // Set events
            self.eventHandler();

            if (self.params.uniqueDomain) {
                $('#domain', content).get(0).selectedIndex = 1;
                self.selectDomain(self, self.params.domains[0]);
            }

            /*if(self.params.goToStep2) {
               self.showStep(2);
            }*/
        });
    },
    whenVisible: function(element, cb) {
        var i_ = setInterval(function () {
            if ($(element).is(':visible')) {
                clearInterval(i_);
                cb();
            }
        }, 1);
    },
    eventHandler: function() {
        var self = this;
        var content = self.getContent();
        var userInput = $("#user", content);
        var passwordInput = $("#password", content);
        var oAuthParams = self.loginFacade.oAuthParameters || {};

        $("#user,#password", content).keypress(function(e) {
            if (e.which == 13) {
                $("#btn-login", content).click();
            }
        });

        /* Interval for set Info from Browser*/
        self.interval = setInterval(function() {
            var form = $('form');
            if (form[0].checkValidity()) {
                $("#btn-login", form).addClass('valid');
            } else {
                $("#btn-login", form).removeClass('valid');
            }
        }, 500);

        $("#btn-login", content).on("click", function (e) {
            $('#login-message-wrapper').text('');

            var element = e.target;
            if($(element).hasClass('valid')) {
                var loginOption = "alwaysAsk";

                self.generalAuthentication({
                    user: userInput.val(),
                    password: passwordInput.val(),
                    domain: self.params.selectedDomain,
                    loginOption: loginOption,
                    type: oAuthParams.type || "",
                    oAuth2InternalState: oAuthParams.oAuth2InternalState || "",
                    keepAuthentication: oAuthParams.oauth2sso || "true"
                });
            }
        });

        // Select domain
        $("#domain", content).change(function () {
            var domain = this.value;

            if(self.isValidDomain(domain)) {
                self.selectDomain(self, domain);
            }
        });

        $("#action_goto_step1", content).click(function () {
            self.showStep(1);
        });

        /* Actions for smartphones y tabletas */
        if (bizagi.util.detectDevice() !== "desktop") {
            $('#user,#password', content).on('focus', function(e) {
                var element = e.target;
                $(element).closest('.bzg-input-tm').find('.control-label').addClass('valid');
            });
            $('#user,#password', content).on('focusout', function(e) {
                var element = e.target;
                var val = element.value;
                if (val === '') {
                    $(element).closest('.bzg-input-tm').find('.control-label').removeClass('valid');
                }
            });
        }

        userInput.click(function () {
            self.removeReadOnly(userInput, true);
        });
    },
    /**
     *
     * @param self
     * @param {string} domain
     */
    selectDomain: function(self, domain) {
        self.dataService.defaultParams.runMultipleAuthenticationType({ domain: domain }).done(function (response) {
            self.params.selectedDomain = domain;

            switch (response) {
                case "BIZAGI_LOGIN_WIDGET_QUICKLOGIN":
                    self.loginFacade.changeWidget("BIZAGI_LOGIN_WIDGET_QUICKLOGIN", {
                        selectedDomain: self.params.selectedDomain
                    });
                    break;
                default:
                    console.log("response", response);
                    $("#domain").val(self.params.selectedDomain);
                    self.showStep(2);
                    break
            }
        }).fail(function (err) {
            self.messageController.showWidgetCustomMessage("generic", "error");
        })
    },
    isValidDomain: function(domain) {
        var self = this;

        if(typeof domain === "string" && domain.trim().length > 0) {
            return self.params.domains.filter(function (d) {
                return d.value === domain
            }).length > 0
        }
        return false;
    },
    showStep: function(stepNumber) {
        var self = this;

        if(!self.params.noDomains) {
            if (self.messageController) {
                self.messageController.cleanErrorsQueue();
                self.messageController.showErrors();
                self.messageController.cleanSuccessQueue();
                self.messageController.showSuccess();
            }

            var content = self.getContent();

            function changeParagraphText(text) {
                $(".login-field__title + p", content).text(text);
            }

            switch (stepNumber) {
                case 1:
                    $(".bzg-login_multiple__options", content).hide();
                    $(".bzg-login_multiple__actions", content).hide();
                    $("#action_goto_step1", content).style("display", "none");
                    $(".login-external-credentials", content).hide();
                    $("#domain", content).prop("disabled", false);
                    changeParagraphText(bizagi.localization.getResource("workportal-widget-login-multiple-first-select-domain"));
                    $('#domain', content).get(0).selectedIndex = 0; // reset value selected
                    break;
                case 2:
                    $(".bzg-login_multiple__options", content).show();
                    $(".bzg-login_multiple__actions", content).show();
                    $("#action_goto_step1", content).style("display", "flex");
                    $(".login-external-credentials", content).show();
                    var domain = self.params.domains.filter(function (item) {
                        return item.value === self.params.selectedDomain
                    })[0];
    
                    $("#domain", content)
                        .prop("disabled", true)
                        .prop("title", domain ? domain.displayName + " (" + domain.value + ")" : "");
                    changeParagraphText(bizagi.localization.getResource("workportal-widget-login-multiple-enter-username-password"));
                    break;
                default:
                    break;
            }
        }
    },
    /**
     *
     * @param {object} userInput
     * @param {boolean} [setFocus]
     */
    removeReadOnly: function(userInput, setFocus) {
        if(userInput.length > 0 && userInput[0].hasAttribute('readonly')) {
            userInput.removeAttr('readonly');
            if (typeof setFocus === "undefined" || setFocus === true) {
                userInput.blur();
                userInput.focus();
            }
        }
    },
    generalAuthentication: function(params) {
        var self = this;

        if (params.user === "") {
            self.messageController.addErrorMessage(bizagi.localization.getResource("login-user-name-empty"));
        }
        if (params.password === "") {
            self.messageController.addErrorMessage(bizagi.localization.getResource("login-user-password-empty"));
        }

        if (!self.messageController.hasErrors()) {
            $.when(self.dataService.userLogin(params)).done(function (response) {
                var authType = self.params.authenticationType;
                if (response && response.credentialSuccessForMFA && (authType.toLowerCase() === "bizagi" || authType === "Mixed" || authType === "Windows" || authType === "Multiple")) {
                    window.clearInterval(self.interval);
                    self.dataService.paramsUser = params;
                    self.loginFacade.changeWidget('BIZAGI_LOGIN_WIDGET_REQUESTCODE', {
                        selectedDomain: self.params.selectedDomain,
                        response: response
                    });
                    return;
                }

                if (bizagi.util.parseBoolean(response.isAuthenticate)) {
                    window.clearInterval(self.interval);
                    self.loginFacade.setBizagiAuthentication(response);
                } else {
                    var param = "";
                    if (response.paramError) {
                        param = response.paramError;
                    } else if (params.user) {
                        param = params.user;
                    }

                    self.showError(response.status, "error", [param]);
                }
            }).fail(function() {
                self.messageController.showWidgetCustomMessage("generic", "error");
            });
        } else {
            self.messageController.showErrors();
        }
    },
    getParameterByName: function (name, url) {
        if(!url) url = window.location.href;
        name = name.replace(/[\[\]]/g, "\\$&");
        var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
            results = regex.exec(url);
        if(!results) return null;
        if(!results[2]) return '';
        return decodeURIComponent(results[2].replace(/\+/g, " "));
    },
    /**
     *
     * @param {string} messageStatus
     * @param {string} messageType
     * @param {string[]} [params]
     */
    showError: function(messageStatus, messageType, params) {
        var self = this;
        messageStatus = (messageStatus || '').toLowerCase();

        switch (messageStatus) {
            case 'error':
                self.messageController.showWidgetCustomMessage("generic", messageType, params);
                break;
            default:
                self.messageController.showWidgetCustomMessage(messageStatus, messageType, params);
        }
    }
});
