/**
 *   Name: Bizagi Localization Plugin
 *   Author: Nelson Daza
 *   Comments:
 *   -   This script creates a plugin with an options hash which defines the json resources to localize
 */


// Create or Define BizAgi namespace
bizagi = bizagi || {};
bizagi.l10n = bizagi.l10n || {};

bizagi.l10n.extend("bizagi.l10n",
// Static properties
    {},
// Methods
    {
    /*
     *   Constructor
     */
    init: function (resourceLocationDefinition) {
            this._super(resourceLocationDefinition);
        this.localResourcesLoaded = false;
    },
    /**
     * This method loads or returns a cached version of a dictionary for a given language
     * @override
     * @param language string
     * @returns {*}
     * @private
     */
    _getDictionary: function (language) {
        var self = this;
        var defer = new $.Deferred();

        language = language.toLowerCase();

        // Load resource from web
        var userLanguage = self._getUserLanguage(language);

        // Gets the base language
        var lang = language.toLowerCase().replace(/([^a-z]+)/g, "-").split("-").shift();

        self._loadFullLocalResources(userLanguage).done(function () {

            self.service.getServerResourceDictionary(userLanguage)
                .then(function (dictionary) {
                    var dictLang = self.dictionaries[lang];
                    self.dictionaries[language] = $.extend({}, dictionary, dictLang);
                }).always(function () {

                self._loadMobileLocalDictionary(userLanguage).always(function () {
                    self.dictionaries[language].mobile = true;
                    defer.resolve(self.dictionaries[language]);
                });

            });

        });

        return defer.promise();
    },

    /**
     * Loads the full file resources file
     * @private
     */
    _loadFullLocalResources: function (language) {
        var self = this;
        var defer = new $.Deferred();

        var lang = language.toLowerCase().replace(/([^a-z]+)/g, "-").split("-").shift();

        if (self.localResourcesLoaded) {
            defer.resolve();
        } else {
            self.service.getResourceDictionary("jquery/resources/mobile/bizagi.resources.all.json.txt?build=" + bizagi.loader.build)
                .done(function (resources) {
                    self.dictionaries = resources;
                    self.dictionaries[language] = self.dictionaries[lang];
                    self.localResourcesLoaded = true;
                    defer.resolve();
                });
        }
        return defer.promise();
    },

    /**
     * Loads mobile resources
     * @param language string
     * @private
     */
    _loadMobileLocalDictionary: function (language) {
        var self = this;
        var defer = new $.Deferred();
        language = language.toLowerCase();

        var lang = language.toLowerCase().replace(/([^a-z]+)/g, "-").split("-").shift();

        if (self.localResourcesLoaded && self.dictionaries[lang].mobile) {
            defer.resolve();
        } else {
            self.service.getResourceDictionary("jquery/resources/mobile/bizagi.resources." + lang + ".json.txt?build=" + bizagi.loader.build)
                .done(function (dictionary) {
                    self.dictionaries[language] = $.extend(dictionary, self.dictionaries[language]);
                    defer.resolve(self.dictionaries[language]);
                })
                .fail(function () {
                    self.service.getResourceDictionary("jquery/resources/mobile/bizagi.resources.en.json.txt?build=" + bizagi.loader.build)
                        .done(function (dictionary) {
                            self.dictionaries[language] = $.extend(dictionary, self.dictionaries[language]);
                        })
                        .fail(function (_, status, errorMessage) {
                            console.log("Can't load mobile localization file for language: " + language + ", " + status + " - " + errorMessage); // jshint ignore:line
                        }).always(function () {
                        defer.resolve(self.dictionaries[language]);
                    });
                });
        }

        return defer.promise();
    },

    /**
     * This method returns the localized version of the resource
     * @override
     * @param resource string Key of the resource
     * @param defaultValue string Default value in case key does not exists
     * @returns string resource found or default value
     */
    getResource: function (resource, defaultValue) {
        var self = this;

        // Return original resource when not found on dictionary
        if (self.dictionary[resource] === undefined) {
            return (defaultValue !== undefined ? defaultValue : resource);
        }

        // if found return translated resource
        return self.dictionary[resource];
    },

    _getUserLanguage: function (language) {
        var self = this;

        // Load resource from web
        var userLanguage = language.toLowerCase();

        if (self.resourceLocationDefinition[userLanguage] == null && userLanguage.length > 0) {
            // get all the components of the language
            var userLanguageParts = userLanguage.split("-");
            // Check if any of the language fits (i.e. Norwegian is No, but there are nn-no variants)
            for (var i = userLanguageParts.length - 1; i >= 0; i--) {
                if (self.resourceLocationDefinition[userLanguageParts[i]] != null) {
                    userLanguage = userLanguageParts[i];
                    break;
                }
            }
        }

        if (userLanguage.length == 0 || self.resourceLocationDefinition[userLanguage] == null) {
            // Return default language
            bizagi.log("No resource definition location has been given for language :" + language + ", loading default language");
            userLanguage = "en";
        }

        return userLanguage;
    }

});
